import { PAGE_SIZE } from '@/assets/config'
import { CODE, SelectDataSource, StudentStudyKeys } from '@/assets/constant'
import tabsService from '@/assets/template/tabs'
import utilizeService from '@/assets/template/utilize'
import dataService from '@/assets/method/data'
import Cache from '@/assets/cache'
import Service from '@/assets/service/service'
export default {
  name: 'studentStudyTable',
  data () {
    return {
      code: CODE.StudentStudy,
      dataKeys: StudentStudyKeys,
      items: [],
      total: 0,
      page: 1,
      pageSize: PAGE_SIZE,
      operation: {},
      service: Service,
      condition: {
        page_num: PAGE_SIZE
      },
      searchOptions: [
        { label: '学习时间', key: 'time', type: 'date' },
        { label: '姓名', key: 'name' },
        { label: '学号', key: 'study_num' },
        { label: '专业', key: 'specialty' },
        { label: '导师', key: 'teacher' },
        { label: '境内/外', key: 'type', selectOption: SelectDataSource.Territory },
        { label: '地点', key: 'address' },
        { label: '学习学校', key: 'school' },
        { label: '项目名称', key: 'project' }
      ]
    }
  },
  methods: {
    publicSearch (condition) {
      for (const key in condition) {
        this.condition[key] = condition[key]
      }
      this.getDataTables(this.page)
    },
    search () {
      this.getDataTables()
    },
    pageChange (page) {
      this.getDataTables(page)
    },
    getDataTables (page = 1) {
      this.pageSize = this.condition.page_num
      const request = {

      }
      if (page) {
        request['page'] = page
      }
      for (const key in this.condition) {
        if (this.condition[key]) {
          request[key] = this.condition[key]
        }
      }

      Service.StudentStudy.get(request).then(response => {
        if (response.code == 200) {
          this.page = page
          this.items = response.data.data
          this.total = response.data.total
          setTimeout(() => utilizeService.iCheck(), 200)
        }
      })
    },
    download (item) {
      this.fileExport({
        ids: [item.id],
        model: this.code
      })
    },
    fileExport (request) {
      Service.FileDownload(request).then(response => {
        dataService.download(response)
      })
    },
    delConfirm (item) {
      Service.StudentStudy.del(item.id).then(response => {
        if (response.code == 200) {
          this.getDataTables()
        }
      })
    },
    add () {
      tabsService.addDetailsTab('/views/studentStudy/new', '学生境内/境外交流学习情况-新增')
    },
    edit (item) {
      tabsService.addDetailsTab('/views/studentStudy/' + item.id, '学生境内/境外交流学习情况-详情-' + item.id)
    }
  },
  created () {
    this.operation = Cache.pageOperation('talent_development')
    dataService.getTableKeys(Service, this.code, this.dataKeys).then(dataKeys => {
      this.dataKeys = dataKeys
    })
    if (this.$route.query['key']) {
      this.condition['key'] = this.$route.query['key'];this.condition['status'] = 3
    }
    this.getDataTables()
  },
  mounted () {
  }
}



